.mask{
    width: 100%;
    height: 100vh;
    position: relative;
}
.mask::after{
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: black;
    opacity: 0.8;
}
.intro-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}
.bgImage{
    height: 100%;
    width: 100%;
}
.bgImage .content{
    position: absolute;
    transform: translate(-50%,-50%);
    top: 50%;
    left: 50%;
    text-align: center;
}
.bgImage .content h1{
    font-size: 4rem;
    padding: 0.6rem 0 1.5rem ;
}
.bgImage .content p{
    font-size: 1.4rem;
    font-weight: 200;
    text-transform: uppercase;
}
.content .btn{
    margin: 1rem 0.2rem;
}
@media screen and (max-width:640px){
    .bgImage .content h1{
        font-size: 3rem;
    }
    .bgImage .content p{
        font-size: 1.4rem;
    }
}